.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItem{
    padding: 10px;
}

.dialog{
    font-size: 20px;
    font-family: monospace;
    margin: 20px;
    text-decoration: none;
    color: darkolivegreen;
}

.messages{
    padding: 10px;
    font-size: 15px;
 }

.active{
    color: blueviolet;
    text-decoration: none;
    font-size: 30px;
}